.promo {
  font-family: "Oswald", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #000000;
  line-height: 14px;
  color: #FFFFFF;
  text-align: center;
  padding-top: 27px;
  position: relative;
}
.promo::before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  background: url("../../img/promo/cat.jpg") no-repeat;
}
@media (min-width: 321px) {
  .promo::before {
    background-size: cover;
    background-position: left 50% top 50%;
  }
}
