  .promo::before {
    background: none;
  }
}
@media (min-width: 1440px) {
  .promo::before {
    background: url("../../img/promo/cat--desktop.jpg") no-repeat;
    z-index: 1;
    top: -80px;
    left: 720px;
  }
}
.promo::after {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85) 302px, #FFFFFF 302px, #FFFFFF);
}
@media (min-width: 768px) {
  .promo::after {
