.optional-catalog__wrapper {
  min-width: 280px;

  margin: 0 20px;
  padding: 25px 0 35px;

  @media (min-width: @tablet) {
    margin: 0 30px;
  }

  @media (min-width: @desktop) {
    margin: 0 110px;
    padding: 0;
    padding-bottom: 86px;
  }
}

.optional-catalog__title {
  .title;
  line-height: 24px;

  margin-top: 0;
  margin-bottom: 36px;

  @media (min-width: @tablet) {
    .heading;

    margin-bottom: 80px;
  }
}

.optional-catalog__layout {
  @media (min-width: @desktop) {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.optional-catalog__card {
  margin: 0 auto 10px;

  max-width: 450px;

  border-top: 1px solid @optional-border-color;

  @media (min-width: @tablet) {
    border-top: none;
    border: 1px solid @optional-border-color;
    border-bottom: none;

    max-width: 1000px;
    margin: 0 auto;
  }

  @media (min-width: @desktop) {
    max-width: 896px;

    border-left: none;
    border-right: none;
  }
}

.optional-catalog__card--additional-border-bottom {
  border-bottom: 1px solid @optional-border-color;

  padding-bottom: 19px;
}
